<template>
<section>
  <el-row>
    <el-col :span="4">
      <span class="top-nav-link" @click="goBack()">返回上一页</span>
    </el-col>
  </el-row>  
  <el-row>
    <el-col :offset="1" :span="9">
      <el-input v-model="dialectical" 
      placeholder="拼音码查询"
      @blur="handelSearch" clearable
      @keydown.enter.native="handelSearch"
      suffix-icon="el-icon-search"></el-input>
    </el-col>
    <el-col :offset="4" :span="9">
      <el-input v-model="chineseMedicine" 
      placeholder="中医病名查询"
      @blur="handelSearch" clearable
      @keydown.enter.native="handelSearch"
      suffix-icon="el-icon-search"></el-input>
    </el-col>
    <el-col style="margin-top:24px">
      <el-table 
      @cell-dblclick="selectItem"
      :data="tableData">
        <el-table-column  label="症形" prop="dialecticalName"></el-table-column>
        <el-table-column  label="治法" prop="cureName"></el-table-column>
        <el-table-column  label="主症" prop="mainSymptomName"></el-table-column>
        <el-table-column  label="中医病名" prop="chineseMedicineName"></el-table-column>
      </el-table>
      <div :span="24" class="toolbar">
        <el-pagination background layout="total, prev, pager, next, jumper" @current-change="handlePageChange" :current-page="tableArgs.pageNumber" :page-size="tableArgs.pageSize" :total="total" style="float:right;">
        </el-pagination>
      </div>
    </el-col>
  </el-row>
</section>
</template>
<script>
import { 
  getSelectDCTable,
  getSelectAnorectalDialecticalCure,
  updateEmpBasic,
 } from "../../api";
import {mapMutations} from 'vuex'
export default {
  data(){
    return{
      dialectical: '', // 拼音码查询
      chineseMedicine: '', // 中医病名查询
      tableData: [], // 表单数据
      total: 0, // 数据条数
      tableArgs:{
        pageSize: 10,
        pageNumber: 1,
      }
    }
  },
  methods:{
  ...mapMutations('caseNote',['saveTreatmentInfo']),
    getData(){
      let westernMedicineName = sessionStorage.getItem('zhzy_gcxt_xybm_name')
      if(westernMedicineName){
        getSelectAnorectalDialecticalCure({
         // deptCode: sessionStorage.getItem('current_deptCode'),
          deptCode: '10001',
          account: '1',
          westernMedicineName: westernMedicineName,
          pageNumber: this.tableArgs.pageNumber,
          searchText: this.searchTextCode ? this.searchTextCode : this.searchTextName
        }).then(res=>{
          if(res.data.code=='0'){
            this.total = res.data.data.total
            this.tableData = res.data.data.rows
          }
        })
        return
      }
      getSelectDCTable({
        deptCode: sessionStorage.getItem('current_deptCode'),
        account: '1',
        pageNumber: this.tableArgs.pageNumber,
        dialectical: this.dialectical,
        chineseMedicine: this.chineseMedicine
      }).then(res=>{
        if(res.data.code=='0'){
          this.total = res.data.data.total
          this.tableData = res.data.data.rows
        }else{
          this.total = 0
          this.tableData = []
        }
      })
    },
    selectItem(item){ // 双击选择
      // this.saveTreatmentInfo(item)
      // this.$router.push('symptom')
      updateEmpBasic({
        empBasicMainID: sessionStorage.getItem('eid'),
        mainSymptomName: '',
        mainSymptomCode: '',
        diseaseName: '',
        diseaseCode: '',
        tongueName: '',
        tongueCode: '',
        pulseName: '',
        pulseCode: ''
      }).then(res=>{
        if(res.data.code == '0'){
          this.saveTreatmentInfo(item)
          this.$router.push('symptom')
        }else{
          this.$message.error('保存失败')
        }
      })

    },
    handlePageChange(index){ // 页码切换
      this.tableArgs.pageNumber = index
      this.getData()
    },
    handelSearch(){
      this.tableArgs.pageNumber = 1
      this.getData()
    }
  },
  mounted(){
    this.getData()
  }
}
</script>
<style scoped>

</style>



